.app__header {
  display: block;
  margin-bottom: 8px;
  align-items: center;
  color: #f8f8f2;
  background-color: #191A21; }

.tab {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px 10px;
  background: #44475A70;
  border-top: 1px solid #BD93F9;
  display: flex;
  border-right: 1px solid #191A21; }
  .tab.shadow {
    background-color: #21222C; }

.path {
  padding: 4px 10px 6px;
  color: #6272A4;
  background: #44475A70;
  box-shadow: 0 8px 6px -6px black;
  display: flex; }

.path_text {
  margin-right: 8px; }
  .path_text:hover {
    color: #f8f8f2;
    cursor: pointer; }

.xBttn {
  margin-left: 15px;
  margin-right: 10px; }
  .xBttn.shadow {
    color: transparent; }

.tabBar {
  display: flex; }

.link {
  color: #f8f8f2;
  text-decoration: none; }

.homepage {
  /*width: 100%;*/
  height: 200px;
  margin: 0.25em; }

.line {
  display: flex;
  margin-bottom: 1px; }

.n_line {
  padding: 0px 10px;
  width: 20px;
  text-align: end;
  margin-right: 10px; }

.key_text {
  color: #8be9fd; }
  .key_text.moved {
    margin-left: 15px; }

.label_text {
  color: #f1fa8c;
  margin-left: 3px; }

.dots {
  color: #ff79c6;
  margin-right: 5px;
  margin-left: 1px; }

.dash {
  color: #ff79c6;
  margin-right: 5px; }

.page {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content; }

.main {
  height: 100vh;
  width: 100vw; }

body {
  margin: 0;
  font-family: Consolas, 'Courier New', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #313241;
  font-size: 15px;
  color: #f8f8f2; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html {
  scroll-behavior: smooth; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  /*height: 88px;*/
  z-index: 10; }

.pagesContainer {
  /*margin-top: 100px;*/
  margin-top: 60px; }

